import React from 'react';
import PageTemplate from '../PageTemplate/PageTemplate';

const Delivery = () => {
  return (
    <PageTemplate>
      <h1>Delivery</h1>
      <div className="page-underline"></div>
      <div className='page-subtitle'>
        <p>Our product delivers in envelope and it takes approximately 4-5 working days within the EU countries. Once the money in our account appears, we will send you the selected product in the course of 6 hours.</p>

        <p>The drugs will be shipped from EU-countries in envelope, so there won't any be any problems with customs. If you are not at home, you get a notification deposited in your mailbox. The package will be in the post for 1 week.</p>

        <p>The orders more than 40 pills could be sent in multiple packets which may not arrive on the same day. Our shop takes no
        responsibility for delay of delivery, the lengthy processing, the lack of notification about the unsuccessful delivery.</p>
      </div>
    </PageTemplate>
  );
};

export default Delivery;
