import React from 'react';
import PageTemplate from '../PageTemplate/PageTemplate';

const Article = () => {
  return (
    <PageTemplate>
      <h1>Article</h1>
      <div className="page-underline"></div>
      <div className='page-subtitle'>
        <p>Here is Article</p>
      </div>
    </PageTemplate>
  );
};

export default Article;
