import React, { useEffect, useState } from 'react';
import { Container } from 'react-bootstrap';
import productsData from '../../products.json';
import { Link } from 'react-router-dom';

import Breadcrumbs from '../../components/Breadcrumbs/Breadcrumbs';
import './PageTemplate.css';

const PageTemplate = ({ children }) => {
    const [products, setProducts] = useState([]);

    useEffect(() => {
        setProducts(productsData);
    }, []);
    return (
        <>
            <div className="section-catalog">
                <Container>
                    <div className="catalog-container">
                        <div className="catalog-wrapper" data-aos="fade">
                            <Breadcrumbs />
                            {children}
                            <div className="wrapper-btn">
                                <h1 className='wrapper-btn-title' data-aos="fade-right">Continue to shopping</h1>
                                <Link to="/#catalog" smooth={false} duration={500} className="main-content-btn" data-aos="fade-right">
                                    <div className='btn-text'>Watch Catalog</div>
                                    <div className='btn-icon'>
                                        <img src={`${process.env.PUBLIC_URL}/images/icons/arrow-down-right-white.svg`} alt="" />
                                    </div>
                                </Link>
                            </div>
                        </div>
                        <div className="sidebar" data-aos="fade-left">
                            <h1 className="sidebar-title">All products:</h1>
                            <ul className='sidebar-list'>
                                {products.map((product) => (
                                    // <a href='#' key={product.id}>
                                    //     <li className='sidebar-object'>{product.title}</li>
                                    // </a>
                                    <Link to={`/${product.title}`}>
                                        <li className='sidebar-object'>{product.title}</li>
                                    </Link>
                                ))}
                            </ul>
                        </div>
                    </div>
                </Container>
                <div className="sidebar-bg-wrapper" data-aos="fade-left">
                    <div className="sidebar-bg"></div>
                </div>
            </div>
        </>
    );
};

export default PageTemplate;
