import React from 'react';
import PageTemplate from '../PageTemplate/PageTemplate';

const Payment = () => {
  return (
    <PageTemplate>
      <h1>Payment</h1>
      <div className="page-underline"></div>
      <div className='page-subtitle'>
        <p>You can pay for Drugs by transferring funds to a Bank account or a credit card.</p>

        <p>For faster transfers, please use Online Banking. If you pay online, funds will be transferred within 24 hours.</p>

        <p>Once the money appears in our account, we will send you the selected product within 6 hours.</p>

        <p>To order the Drugs on our Website, please put the desired items in the shopping cart and go to checkout. Then fill in all the fields and click on “Order.” You will get a confirmation of your order with details for payment by E-Mail.</p>

        <p>We accept payment via:</p>

        <p>– SEPA Credit Transfer on Bank Account <br />
        – Transferring funds directly into our credit card.</p>

        Please specify your Order Number in the notes to the payment, for example, “NO. 75” or just “75.”
      </div>
    </PageTemplate>
  );
};

export default Payment;
