import React, { useState } from 'react'
import { useNavigate, useLocation } from 'react-router-dom';

import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import './Header.css';
// import NavDropdown from 'react-bootstrap/NavDropdown';

function Header() {
    const [searchTerm, setSearchTerm] = useState('');
    const navigate = useNavigate(); // Создаем навигатор
    const location = useLocation();

    const isWhiteHeader = location.pathname === '/';

    const handleSearch = (e) => {
        e.preventDefault();
        navigate(`/search?query=${searchTerm}`); // Используем navigate для перенаправления
    };

    return (
        <Navbar expand="lg" className={`header-navbar ${isWhiteHeader ? 'header-white' : ''}`}>
            <Container>
                <Navbar.Brand href="/Home">
                    <div className="header-logo"></div>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ms-auto align-items-center">
                        <form onSubmit={handleSearch} className="search-form">
                            <input
                                type="text"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                                placeholder="Search..."
                                className="search-input"
                            />
                            <button type="submit" className="search-btn">
                                <img src={`${process.env.PUBLIC_URL}/images/icons/search-btn.svg`} alt="" className="icon-search" />
                            </button>
                        </form>
                        <button className="header-btn cart-btn">
                            <img src={`${process.env.PUBLIC_URL}/images/icons/shopping-cart.svg`} alt="" className="icon-shopping-cart" />
                        </button>
                        <button className="header-btn translate-btn">
                            <img src={`${process.env.PUBLIC_URL}/images/icons/us.png`} alt="" className="icon-shopping-cart" />
                        </button>
                        <button className="header-btn">
                            <img src={`${process.env.PUBLIC_URL}/images/icons/burger-icon.svg`} alt="" className="icon-shopping-cart" />
                        </button>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}

export default Header;