import React from 'react';
import PageTemplate from '../PageTemplate/PageTemplate';
import Accordion from 'react-bootstrap/Accordion';

const FAQ = () => {
  return (
    <PageTemplate>
      <h1>FAQ</h1>
      <div className="page-underline"></div>
      <div className='page-subtitle'>
        <Accordion  className="custom-accordion">
          <Accordion.Item eventKey="0">
            <Accordion.Header>What Does the Term 'Generic' mean?</Accordion.Header>
            <Accordion.Body>
              The term 'generic' in the context of generic medications, means a 'copy' that is bio-identical to brand name medications. Generic medications have the same active chemical compounds and the same rate of absorption as brand name medications.
              <p/>
              The generic medications we supply are high quality products that have the same dosage, strength and quality as brand name medications.
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey="1">
            <Accordion.Header>Why are Generic Medications Cheaper than Brand Name Medications?</Accordion.Header>
            <Accordion.Body>
              Manufacturers of generic medications do not have the costs of research, development,
              marketing, or advertising that brand name manufacturers do. Those savings are passed on
              to you, the customer.
              <p/>
              All of the generic medications we supply are legally manufactured in India.
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey="2">
            <Accordion.Header>Can the Wording on Generic Medication Labels Differ from Brand Name Medications?</Accordion.Header>
            <Accordion.Body>
              Yes. The wording on labels can differ between generic and brand name medications. Any difference in the wording of generic medications is simply to comply with regulations and does not compromise the effectiveness of the medication in any way.
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey="3">
            <Accordion.Header>Can the Shape of Generic Medications Pills Differ from Brand Name Medications?</Accordion.Header>
            <Accordion.Body>
              Yes. The shape of generic medication pills can differ slightly in colour or shape from brand name pills. However, any difference in shape or colour does not compromise the effectiveness of the medication in any way.
            </Accordion.Body>
          </Accordion.Item>
        </Accordion>
      </div>
    </PageTemplate>
  );
};

export default FAQ;
