import React from 'react';
import PageTemplate from '../PageTemplate/PageTemplate';

const Contacts = () => {
  return (
    <PageTemplate>
      <h1>Contacts</h1>
      <div className="page-underline"></div>
      <div className='page-subtitle'>
        Please contact us.
        <br />
        <a href="mailto:info@vitapills.org">
          <img src={`${process.env.PUBLIC_URL}/images/icons/mail-btn.svg`} alt="" />
          info@vitapills.org
        </a>
        <div className="contact-buttons">
          <a href="#" data-aos="fade-up" data-aos-delay="400">
            <button className="btn-instagram">
              <img src={`${process.env.PUBLIC_URL}/images/icons/instagram.svg`} alt="" />
            </button>
          </a>
          <a href="#" data-aos="fade-up" data-aos-delay="600">
            <button className="btn-facebook">
              <img src={`${process.env.PUBLIC_URL}/images/icons/facebook.svg`} alt="" />
            </button>
          </a>
          <a href="#" data-aos="fade-up" data-aos-delay="800">
            <button className="btn-twitter">
              <img src={`${process.env.PUBLIC_URL}/images/icons/twitter.svg`} alt="" />
            </button>
          </a>
        </div>
      </div>
    </PageTemplate>
  );
};

export default Contacts;
