import { useEffect } from 'react';
import Container from 'react-bootstrap/Container';
import { Link } from 'react-scroll';
import { useLocation } from 'react-router-dom';

import Catalog from '../Catalog/Catalog';
import './Home.css';

function Home() {
    const location = useLocation();

    useEffect(() => {
        if (location.hash === '#catalog') {
            const element = document.getElementById('catalog');
            if (element) {
                element.scrollIntoView({ behavior: 'smooth' });
            }
        };
    }, [location]);

    return (
        <div className='main'>
            <section className='main-section'>
                <Container>
                    <div className="badge-wrapper badge-1">
                        <div className="badge-title-block">
                            <span>discreet packaging</span>
                        </div>
                        <img src={`${process.env.PUBLIC_URL}/images/icons/dot.svg`} alt="" className="badge-dot" />
                    </div>
                    <div className="badge-wrapper badge-2">
                        <div className="badge-title-block">
                            <span>anonymous delivery</span>
                        </div>
                        <img src={`${process.env.PUBLIC_URL}/images/icons/dot.svg`} alt="" className="badge-dot" />
                    </div>
                    <div className="badge-wrapper badge-3">
                        <div className="badge-title-block">
                            <span>delivery guarantee</span>
                        </div>
                        <img src={`${process.env.PUBLIC_URL}/images/icons/dot.svg`} alt="" className="badge-dot" />
                    </div>
                    <div className="content-wrapper">
                        <div className="content-title" data-aos="fade-right">
                            <h1>An effective <br /><em>solution</em> to improve <br />male potency!</h1>
                        </div>
                        <div className="content-buttons">
                            <Link 
                            to="section-header" 
                            smooth={false} 
                            duration={500} 
                            className="main-content-btn" 
                            data-aos="fade-up" 
                            data-aos-delay="200">
                                <div className='btn-text'>Watch Catalog</div>
                                <div className='btn-icon'>
                                    <img src={`${process.env.PUBLIC_URL}/images/icons/arrow-down-right.svg`} alt="" />
                                </div>
                            </Link>
                            <div className="contact-buttons">
                                <a href="#" data-aos="fade-up" data-aos-delay="400">
                                    <button className="btn-instagram">
                                        <img src={`${process.env.PUBLIC_URL}/images/icons/instagram.svg`} alt="" />
                                    </button>
                                </a>
                                <a href="#" data-aos="fade-up" data-aos-delay="600">
                                    <button className="btn-facebook">
                                        <img src={`${process.env.PUBLIC_URL}/images/icons/facebook.svg`} alt="" />
                                    </button>
                                </a>
                                <a href="#" data-aos="fade-up" data-aos-delay="800">
                                    <button className="btn-twitter">
                                        <img src={`${process.env.PUBLIC_URL}/images/icons/twitter.svg`} alt="" />
                                    </button>
                                </a>
                            </div>
                        </div>
                    </div>
                </Container>
            </section>
            <Container>
                <div className="section-header"  id="catalog">
                    <div className="section-wrapper wrapper-1">
                        <h1 className="object-title" data-aos="fade-right">Catalog</h1>
                        <div className="object-underline" data-aos="fade-right"></div>
                        <p className="object-subtitle" data-aos="fade-right">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque id semper leo, eu malesuada ante.</p>
                    </div>
                    <div className="section-wrapper wrapper-2">
                        <img src={`${process.env.PUBLIC_URL}/images/icons/quotes.svg`} alt="" data-aos="zoom-in" />
                        <p className="object-subtitle" data-aos="zoom-in">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque id semper leo, eu malesuada ante.</p>
                    </div>
                </div>
            </Container>
            <Catalog />
        </div>
    )
}

export default Home;