import React, { useState, useEffect } from 'react'
import { useNavigate, useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import './Footer.css';
// import NavDropdown from 'react-bootstrap/NavDropdown';

function Header() {
    const [searchTerm, setSearchTerm] = useState('');
    const navigate = useNavigate(); // Создаем навигатор
    const location = useLocation();

    useEffect(() => {
        window.scrollTo(0, 0);  // Прокрутка страницы к началу
    }, [location]); // Срабатывает при каждом изменении пути
    
    const handleSearch = (e) => {
        e.preventDefault();
        navigate(`/search?query=${searchTerm}`); // Используем navigate для перенаправления
    };

    return (
        <Navbar expand="lg" className="footer-navbar">
            <Container>
                <div className="nav-brand-wrapper" data-aos="fade-right">
                    <Navbar.Brand href="/">
                        <img src={`${process.env.PUBLIC_URL}/main-logo.png`} alt="Vitapills" />
                    </Navbar.Brand>
                </div>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mx-auto" data-aos="zoom-in-up">
                        <Nav.Link as={Link} to="/payment">Payment</Nav.Link>
                        <Nav.Link as={Link} to="/delivery">Delivery</Nav.Link>
                        <Nav.Link as={Link} to="/article">Article</Nav.Link>
                        <Nav.Link as={Link} to="/faq">FAQ</Nav.Link>
                        <Nav.Link as={Link} to="/contacts">Contacts</Nav.Link>
                    </Nav>
                    <Nav className="align-items-center" data-aos="fade-left">
                        <form onSubmit={handleSearch} className="search-form">
                            <input
                                type="text"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                                placeholder="Search..."
                                className="search-input"
                            />
                            <button type="submit" className="search-btn">
                                <img src={`${process.env.PUBLIC_URL}/images/icons/search-btn-white.svg`} alt="" className="icon-search" />
                            </button>
                        </form>
                        <button className="footer-btn cart-btn">
                            <img src={`${process.env.PUBLIC_URL}/images/icons/shopping-cart.svg`} alt="" className="icon-shopping-cart" />
                        </button>
                    </Nav>
                </Navbar.Collapse>
            </Container>
            <div className="footer-menu-underline" data-aos="zoom-in-up"/>
            <Container className='footer-submenu'>
                <div className="submenu-wrapper" data-aos="fade-right">
                    Vitapills.org <br />
                    All rights reserved.
                </div>
            </Container>
            <Container>
                <div className="submenu-buttons">
                    <a href="#" data-aos="fade">Privacy Policy</a>
                    <a href="#" data-aos="fade" data-aos-delay="200">Terms of Use</a>
                </div>
            </Container>
        </Navbar>
    )
}

export default Header;