import React from 'react';
import PageTemplate from '../PageTemplate/PageTemplate';

function ProductPageDescription({ product }) {
    return (
        <PageTemplate>
            <h1>{product.title}</h1>
            <img src={product.imageUrl} alt={product.title} />
            <p>{product.description}</p>
            <p>{product.additionalDescription}</p>
            <h3>Цена: {product.price.from} - {product.price.to} руб.</h3>
            <h4>Варианты:</h4>
            <ul>
                {product.options.map(option => (
                    <li key={option.variant}>
                        {option.variant} - {option.price} руб.
                    </li>
                ))}
            </ul>
        </PageTemplate>
    );
}

export default ProductPageDescription;
