import React, { useEffect, useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import productsData from '../../products.json';
import './Catalog.css';
import { Element } from 'react-scroll';
import PageTemplate from '../PageTemplate/PageTemplate';
import { Link } from 'react-router-dom';

function Catalog() {
  const [products, setProducts] = useState([]);

  useEffect(() => {
    // Загрузка данных из JSON файла
    setProducts(productsData);
  }, []);

  return (
    <Element name="section1">
      <PageTemplate>
        <Row xs={1} sm={2} md={3} className="g-6">
          {products.map((product) => (
            <Col key={product.id}>
              <Card>
                <Card.Img variant="top" src={`${process.env.PUBLIC_URL}/${product.imageUrl}`} alt={product.title} />
                <Card.Body>
                  <Card.Title>{product.title}</Card.Title>
                  <Card.Text>
                    €{product.price.from} - €{product.price.to}
                  </Card.Text>
                  <Link to={`/${product.title}`} className='btn'>Select Options</Link>
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      </PageTemplate>
    </Element>
  )
}

export default Catalog;